export default {
  data() {
    return {
      items: [
        {
          pergunta: 'O diagrama possui no mínimo 4 classes?',
          resposta: true,
          disabled: false,
        },
        {
          pergunta: 'Todas as classes possuem associação?',
          resposta: true,
          disabled: false,
        },
        {
          pergunta: 'O diagrama possui um objeto Interface?',
          resposta: true,
          disabled: false,
        },
        {
          pergunta: 'Todas as classes possuem módulos?',
          resposta: true,
          disabled: false,
        },
        {
          pergunta: 'Os elementos têm as relações certas e ou coerentes?',
          resposta: true,
          disabled: false,
        },
        {
          pergunta: 'Os nomes das classes são coerentes?',
          resposta: true,
          disabled: false,
        },
        {
          pergunta: 'O diagrama possui um controlador?',
          resposta: true,
          disabled: false,
        },
        {
          pergunta: 'As classes possuem atributos?',
          resposta: true,
          disabled: false,
        },
      ],
      realizada: [],
      valorTotal: 0,
      snackbar: false,
      recomecarBtn: false
    }
  },
  watch: {
    realizada() {
      if (this.realizada.length == 8) {
        this.recomecarBtn = true
      }
    }
  },
  methods: {
    computaDados(valor, key) {
      if (this.items[key].resposta === valor) {
        this.items[key].disabled = true
        this.valorTotal = this.valorTotal + 12.5
        this.realizada.push(1)
      } else {
        this.items[key].disabled = true
        this.snackbar = true
        this.realizada.push(1)
      }
    },
    recomecar() {
      this.realizada = []
      this.valorTotal = 0
      this.recomecarBtn = false
      this.items.forEach(f => {
        f.disabled = false
      });
    }
  }
}